// Profile.js

import React, { useContext, useState, useEffect } from 'react';
import { UserContext } from '../Contexts/UserContext';// Adjust the path based on your actual file structure
 // Adjust the path based on your actual context location
import './ProfilePage.css'; // Import your CSS file for styling

const Profile = () => {
  const { user } = useContext(UserContext); // Access user from UserContext
  const [profileData, setProfileData] = useState({
    posts: 0,
    followerCount: 0,
    followingCount: 0,
    bio: '',
    username: user ? user.email.split('@')[0] : 'username' // Extract username from email as an example
  });

  useEffect(() => {
    const fetchProfileData = async () => {
      if (!user) return;

      try {
        // Fetch additional profile data (posts, followerCount, followingCount, bio) based on user.id
        console.log(user)
        const response = await fetch(`https://windy-worm-20-cytkse7yeh4q.deno.dev/followers/${user.id}`);
        if (response.ok) {
          const data = await response.json();
          console.log(data)
          setProfileData({
            posts: data.posts,
            followerCount: data.followerCount,
            followingCount: data.followingCount,
            bio: data.bio,
            username: user.email.split('@')[0]
          });
        } else {
          console.error('Failed to fetch profile data:', response.statusText);
        }
      } catch (error) {
        console.error('Error fetching profile data:', error);
      }
    };

    fetchProfileData();
  }, [user]); // Execute when user context changes

  return (
    <div className="profile-container">
      <div className="profile-header">
        <img
          className="profile-picture"
          src="https://via.placeholder.com/150"
          alt="Profile"
        />
        <div className="profile-user-info">
          <h1 className="profile-username">{profileData.username}</h1>
          <div className="profile-stats">
            <p><strong>Posts:</strong> {profileData.posts}</p>
            <p><strong>Followers:</strong> {profileData.followerCount}</p>
            <p><strong>Following:</strong> {profileData.followingCount}</p>
          </div>
          <p className="profile-bio">{profileData.bio}</p>
        </div>
      </div>
      {/* Additional sections like photos, posts, etc. can be added here */}
    </div>
  );
};

export default Profile;
