import React, { useContext } from 'react';
import { Layout, Menu, Button, Avatar, Typography } from 'antd';
import { UserOutlined } from '@ant-design/icons'; // Import UserOutlined from Ant Design
import { UserContext } from '../Contexts/UserContext';
import { useHistory } from 'react-router-dom';
import ProfilePage from './Profile'; // Import the ProfilePage component
import '../App.css'; // Import the global app.css file

const { Header, Content, Footer } = Layout;
const { Title } = Typography;

const Home = () => {
  const { user, logout } = useContext(UserContext);
  const history = useHistory();

  const handleLogout = () => {
    logout();
    history.push('/login');
  };

  return (
    <Layout className="layout">
      <Header className="header">
        <div className="logo">
          <Avatar size="large" icon={<UserOutlined />} />
        </div>
        <Menu theme="dark" mode="horizontal" defaultSelectedKeys={['1']} className="menu">
          <Menu.Item key="1">Home</Menu.Item>
          <Menu.Item key="2">Explore</Menu.Item>
          <Menu.Item key="3">Notifications</Menu.Item>
          <Menu.Item key="4" className="menuItem">
            <Button type="primary" onClick={handleLogout}>Logout</Button>
          </Menu.Item>
        </Menu>
      </Header>
      <Content className="content">
        <div className="contentInner">
          <Title level={2}>Welcome, {user.username}!</Title>
          {/* Example content from ProfilePage */}
          <ProfilePage />
        </div>
      </Content>
      <Footer className="footer">Your Social Media App 2024 Created by You</Footer>
    </Layout>
  );
};

export default Home;
